import React from 'react'
import PageTitle from './../shared/page-title';
import SectionTitle from './../shared/section-title';
import Paragraph from './../shared/paragraph';
import PageIntro from '../shared/page-intro';
import Link from './../shared/link';
import Comments from './../shared/comments';
import InlineCode from './../shared/inline-code';
import Code from './../shared/code';

import logo from './../img/hwcursor/logo.png';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text="Unity3D Hardware Cursor"/>

                <PageIntro logo={logo}>
                    <Paragraph>
                        A Unity library for changing system cursor image - the best way to make in-game cursor
                        fast, responsive and framerate-independent. Library works with Windows and Mac OS X
                        standalone builds, does not require Unity PRO. Can load cursor images from both
                        files and memory (therefore, from assets too).
                    </Paragraph>
                    <Paragraph>
                        <strong>Note:</strong> intended to be used with Unity3d versions prior 4.0.
                        Unity v.4.0 has native support for hardware cursor.
                    </Paragraph>
                    <Paragraph>
                        <Link to="http://u3d.as/3eH">Get from Unity Asset Store</Link>.
                    </Paragraph>
                </PageIntro>

                <SectionTitle text="Installation"/>

                <Paragraph>
                    Copy <InlineCode>HardwareCursor.dll</InlineCode> into your project Assets folder.
                </Paragraph>

                <SectionTitle text="Usage"/>

                <Paragraph>
                    Create cursor, initialize it from file and set as active:
                </Paragraph>

                <Code lang="csharp">
{`using UnityEngine;
using HardwareCursor;
///...
var cursor = Cursor.Create();
var hotSpotX = 0f;
var hotSpotY = 0f;
cursor.InitFromFile("cursor.cur", hotSpotX, hotSpotY); // supports CUR, ICO, PNG
cursor.SetActive();
///...`}
                </Code>

                <Paragraph>
                    Create cursor, initialize it from an asset and set as active:
                </Paragraph>

                <Code lang="csharp">
{`using UnityEngine;
using HardwareCursor;
//...
var data = (Resources.Load("Textures/cursor") as Texture2D).EncodeToPNG();
var hotSpotX = 0f;
var hotSpotY = 0f;
cursor.InitFromMemory(data, hotSpotX, hotSpotY);
cursor.SetActive();
//...`}
                </Code>

                <Paragraph>
                    Reset to default cursor:
                </Paragraph>

                <Code lang="csharp">
                    {`cursor.ActivateDefaultCursor();`}
                </Code>

                <Paragraph>
                    <strong>Note:</strong> to be able to use texture asset as a cursor image, the texture
                    should be imported with Advanced texture type, should have Read/Write enabled set to
                    true and its format set to ARGB 32 bit.
                </Paragraph>

                <SectionTitle text="Known bugs"/>

                <Paragraph>
                    <ul>
                        <li>
                            Does not work in editor, only in standalone builds.
                        </li>
                        <li>
                            Under Mac OS X the cursor is reset to default if game resolution is changed in runtime.
                            You can work around this by re-activating the cursor.
                        </li>
                    </ul>
                </Paragraph>

                <Comments/>
            </div>
        );
    }
}


